<template>
  <section class="denuncias">
    <div class="contactForm" v-if="!sendingEmail">
      <div class="contactInfo">
        <h2>
          Tens algo a denunciar?
        </h2>
        <p> Não te preocupes, porque não estás sozinh@. Estamos aqui para te ajudar. <br /> Toda a informação que nos for transmitida será mantida em anonimato! <br />
            Não fiques em silêncio, contacta-nos!</p>
        <div class="contactIcons">
          <ul>
            <a href="mailto:gape@aaue.pt">
              <i class="fas fa-envelope" aria-hidden="true"
                ><span>Email</span></i
              >
              <li>gape@aaue.pt</li>
            </a>
          </ul>
        </div>
      </div>
      <form action="POST" @submit.prevent="sendFormData">
        <label for="Contacto">Nome / Contacto <span>(Não é obrigatório)</span></label>
        <input
          type="text"
          placeholder="Nome"
          id="name"
          v-model="name"
        />
        <label for="mensagem">Mensagem <span>*</span></label>
        <textarea
          class="mensagem"
          placeholder="Mensagem"
          id="mensagem"
          v-model="mensagem"
          required
        />
        <div class="buttons" :class="{ isActive: isFormFilled }">
          <Button
            buttonText="Enviar mensagem"
            class="g-recaptcha"
            data-sitekey="6LcQvp4cAAAAAGNx5TXt5zWBQ1lHd_mhHyit-Plq"
            data-callback="onSubmit"
            data-action="submit"
          />
        </div>
      </form>
    </div>
    <div class="modal" v-else>
      <img
        v-if="!emailSent && !emailFailed"
        src="@/assets/api/spinning-circle.gif"
        alt=""
      />
      <div v-else-if="emailSent" class="sucess">
        <h1>Mensagem enviada</h1>
        <i class="far fa-check-circle sucessIcon"></i>
        <Button buttonText="Voltar" @click="resetEmailSendingValues" />
      </div>
      <div v-else-if="emailFailed" class="failure">
        <h1>Ocorreu um erro</h1>
        <i class="fas fa-exclamation-circle errorIcon"></i>
        <p>
          Por favor volte a tentar mais tarde ou envie uma mensagem para
          gape@aaue.pt
        </p>
        <Button buttonText="Voltar" @click="resetEmailSendingValues" />
      </div>
    </div>
  </section>
</template>

<script>
import { ref, watch } from "vue";
import Button from "../components/Button.vue";

export default {
  name: "Denuncias",
  data() {
    return {
      emailSent: false,
      emailFailed: false,
      sendingEmail: false,
      name: "",
      email: "",
      mensagem: "",
      isFormFilled: false,
    };
  },
  setup() {
    let name = ref("");
    let mensagem = ref("");
    let email = ref("");
    let isFormFilled = ref(false);

    watch([name, mensagem], () => {
      if (
        mensagem.value
      ) {
        isFormFilled.value = true;
      } else {
        isFormFilled.value = false;
      }
    });

    return {
      isFormFilled: isFormFilled,
      name: name,
      email: email,
      mensagem: mensagem,
    };
  },
  components: {
    Button,
  },
  methods: {
    async sendFormData() {
      if (!this.isFormFilled) {
        return;
      }

      this.sendingEmail = true;

      const formData = {
        sendTo: "denuncias@aaue.pt",
        subject: "Denúncias",
        message: {
          origem: "AAUE.pt",
          name: this.name ? this.name: 'Anónimo',
          email: "Anónimo",
          text: this.mensagem,
        },
      };

      const requestOptions = {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      };

      await fetch(
        "https://blogposting-api.herokuapp.com/api/sendEmail",
        requestOptions
      )
        .then(async (data) => {
          if (data.ok) {
            this.emailSent = true;
            this.clearFormInfo();
          }
        })
        .catch((error) => {
          this.emailFailed = true;
          console.log(error.message);
        });
    },
    clearFormInfo() {
      this.name = "";
      this.mensagem = "";
    },
    resetEmailSendingValues() {
      this.sendingEmail = false;
      this.emailSent = false;
      this.emailFailed = false;
    },
  },
};
</script>

